@import url("https://use.typekit.net/cbo8dqk.css");

$display-font: 'base-900-sans', serif;
$display-font-weight: 300;
//$display-font: 'ltc-bodoni-175', serif
$body-font: 'base-mono-narrow', sans-serif;

$c-red: #D34E24;
$c-light: #F2F2F2;
$c-dark: #262626;
$c-lightblue: #4392F1;
$c-darkblue: #7A215F;

$c-button-1: $c-darkblue;

@mixin padding-hz($x) {
    padding-left: $x;
    padding-right: $x; }

@mixin margin-hz($x) {
    margin-left: $x;
    margin-right: $x; }

@mixin margin-vt($x) {
    margin-top: $x;
    margin-bottom: $x; }


@mixin transform($scale) {
    -webkit-transform: scale($scale);
    -ms-transform: scale($scale);
    transform: scale($scale); }

div {
    transition: all 0.15s ease; }

body {
    font-family: $body-font;
    font-weight: $display-font-weight;
    background-color: $c-light; }

h1 {
    font-family: $display-font;
    font-weight: $display-font-weight;
    font-size: 45pt; }

.center {
    text-align: center;
    height: 120%;
    @include padding-hz(5%); }

.main-div {
    text-align: center; }

#main-div-1 {
    display: inline-block; }

#main-div-2 {
    @include margin-vt(25px); }

.button {
    text-align: center;
    color: $c-light;
    background: $c-button-1;
    display: inline-block;
    font-size: 30pt;
    font-weight: 700;
    padding: 20px 25px;
    border-radius: 10px; }

.icon-sign {
    padding-top: 50px;
    max-width: 200px;
    max-height: 200px; }

a:link {
    text-decoration: none; }

@media only screen and (min-width: 1100px) {
    h1 {
        font-size: 33pt; }

    #main-div-1 {
        width: 700px; }

    #main-div-2 {
        @include margin-vt(20px); }

    .button {
        font-size: 20pt;
        padding: 15px 20px;
        border-radius: 10px;
        background: $c-light;
        border-style: solid;
        border-width: 3px;
        border-color: $c-button-1;
        color: $c-button-1; }

    .button:hover {
        background: $c-button-1;
        color: $c-light; }

    .icon-sign {
        max-width: 150px;
        max-height: 150px; } }

